    &.active {
      opacity: 1;
      visibility: visible;
    }
    a {
      width: 50px;
      height: 50px;
      display: table;
      background: rgba(0,0,0,.5);
      color: $brand-white;
      text-align: center;
      @include border-radius(4px);
      i {
        height: 50px;
        display: table-cell;
        vertical-align: middle;

      }
      &:hover, &:active, &:focus {
