        color: $brand-white;
        font-size: 18px;
        margin-bottom: 15px;
        text-transform: uppercase;
      }
      p {
        color: rgba(255,255,255,.7);
        font-size: 16px;
        line-height: 1.7;
      }
      .icon {
        display: block;
        width: 90px;
        height: 90px;
        margin: 0 auto 10px auto;
        position: relative;
        background: darken($brand-primary, 3%);
        top: -3em;
        @include border-radius(50%);
        i {
          line-height: 90px;
          height: 90px;
          font-size: 40px;
          color: $brand-white;
          &:before {
            display: block;
            text-align: center;
            margin-left: 3px;
          }
        }
      }

      &.feature-1 {
        z-index: 7;
      }
      &.feature-2 {
        bottom: 0px;
        z-index: 10;
        -webkit-box-shadow: 0px 0 30px 10px rgba(0,0,0,0.25);
        -moz-box-shadow: 0px 0 30px 10px rgba(0,0,0,0.25);
        box-shadow: 0px 0 30px 10px rgba(0,0,0,0.25);
        background: darken($brand-primary, 4%);
        @media screen and (max-width: $screen-sm) {
          -webkit-box-shadow: none!important;
          -moz-box-shadow: none!important;
          box-shadow: none!important;
        }
        .icon {
          background: darken($brand-primary, 7%);
        }
        .feature-inner {
          top: -3em;
          position: relative;
          @media screen and (max-width: $screen-sm) {
            top: 0;
          }
        }
        &:before {
          position: absolute;
          top: -50px;
          content: "";
          background: darken($brand-primary, 4%);
          height: 50px;
          width: 100%;
          left: 0;
          @media screen and (max-width: $screen-sm) {
            display: none;
          }
        }
        &:after {
          position: absolute;
          bottom: -30px;
          content: "";
          background: darken($brand-primary, 4%);
          height: 30px;
          width: 100%;
          left: 0;

          // -webkit-box-shadow: 0px 30px 30px 0px rgba(0,0,0,0.25);
          // -moz-box-shadow: 0px 30px 30px 0px rgba(0,0,0,0.25);
          // box-shadow: 0px 30px 30px 0px rgba(0,0,0,0.25);

          @media screen and (max-width: $screen-sm) {
            display: none;
            -webkit-box-shadow: none!important;
            -moz-box-shadow: none!important;
            box-shadow: none!important;
          }
        }

      }
      &.feature-3 {
        z-index: 7;
        background: darken($brand-primary, 8%);
        .icon {
          background: darken($brand-primary, 10%);
        }
      }
    }
  }
  .gtco-flex {
    @include flexwrap;
    @include flex;
    position: relative;
    float: left;
  }

  #gtco-portfolio {
    padding: 7em 0;
    background: #303841;
    min-height: 500px;
    @media screen and (max-width: $screen-sm) {
      padding: 3em 0;
    }
    .gtco-heading {
      h2 {
        color: $brand-white;
      }
      p {
        color: rgba(255,255,255,.7);
      }
    }
  }
  #gtco-portfolio-list {
    @include flexwrap;
    @include flex;
    position: relative;
    float: left;
    padding: 0;
    margin: 0;
    width: 100%;
    li {
      display: block;
      padding: 0;
      margin: 0 0 10px 1%;
      list-style: none;
      min-height: 400px;
      background-position: center center;
      background-size: cover;
      background-repeat: no-repeat;
      float: left;
      clear: left;
      position: relative;
      @media screen and (max-width: $screen-xs) {
        margin-left: 0;
      }
      a {
        min-height: 400px;
        padding: 2em;
        position: relative;
        width: 100%;
        display: block;
        &:before {
          position: absolute;
          top: 0;
          left: 0;
          right: 0;
          bottom: 0;
          content: "";
          z-index: 80;
          @include transition(.5s);
        }
        &.color-1 {
          &:before {
            background: rgba(55, 75, 84, .7);
          }
          &:hover {
            &:before {
              background: rgba(55, 75, 84, 0);
            }
          }
        }
        &.color-2 {
          &:before {
            background: rgba(37, 190, 213, .7);
          }
          &:hover {
            &:before {
              background: rgba(37, 190, 213, 0);
            }
          }
        }

        &.color-3 {
          &:before {
            background: rgba(239, 172, 147, .7);
          }
          &:hover {
            &:before {
              background: rgba(239, 172, 147, 0);
            }
          }
        }
        &.color-4 {
          &:before {
            background: rgba(249, 17, 40, .7);
          }
          &:hover {
            &:before {
              background: rgba(249, 17, 40, 0);
            }
          }
        }
        &.color-5 {
          &:before {
            background: rgba(252, 68, 40, .7);
          }
          &:hover {
            &:before {
              background: rgba(252, 68, 40, 0);
            }
          }
        }
        &.color-6 {
          &:before {
            background: rgba(98, 177, 215, .7);
          }
          &:hover {
            &:before {
              background: rgba(98, 177, 215, 0);
            }
          }
        }
        .case-studies-summary {
          width: auto;
          bottom: 2em;
          left: 2em;
          right: 2em;
          position: absolute;
          z-index: 100;
          @media screen and (max-width: $screen-sm) {
            bottom: 1em;
            left: 1em;
            right: 1em;
          }
          span {
            text-transform: uppercase;
            letter-spacing: 2px;
            font-size: 13px;
            color: rgba(255,255,255,.7);
          }
          h2 {
            color: $brand-white;
            margin-bottom: 0;
            @media screen and (max-width: $screen-sm) {
              font-size: 20px;
            }
          }

        }
      }
      &.two-third {
        width: 65.6%;
        @media screen and (max-width: $screen-sm) {
          width: 49%;
        }
        @media screen and (max-width: $screen-xs) {
          width: 100%;
        }
      }
      &.one-third {
        width: 32.3%;
        @media screen and (max-width: $screen-sm) {
          width: 49%;
        }
        @media screen and (max-width: $screen-xs) {
          width: 100%;
        }
      }
      &.one-half {
        width: 49%;
        @media screen and (max-width: $screen-xs) {
          width: 100%;
        }
      }

    }

  }



  /* Owl Override Style */
  .owl-carousel .owl-controls,
  .owl-carousel-posts .owl-controls, {
    margin-top: 0;
  }
  .owl-carousel .owl-controls .owl-nav .owl-next,
  .owl-carousel .owl-controls .owl-nav .owl-prev,
  .owl-carousel-posts .owl-controls .owl-nav .owl-next,
  .owl-carousel-posts .owl-controls .owl-nav .owl-prev {
    top: 25%!important;
    // margin-top: -39px;
    z-index: 1001;
    position: absolute;
    @include transition(.2s);
  }
  .owl-carousel .owl-controls .owl-nav .owl-next,
  .owl-carousel-posts .owl-controls .owl-nav .owl-next {
    right: -170px;
    @media screen and (max-width: $screen-md) {
      right: -50px;
    }
    // margin-right: -50px;
    &:hover {

    }
  }
  .owl-carousel .owl-controls .owl-nav .owl-prev,
  .owl-carousel-posts .owl-controls .owl-nav .owl-prev {
    left: -170px;
    @media screen and (max-width: $screen-md) {
      left: -50px;
    }
    // margin-left: -50px;
    &:hover {

    }
  }

  .owl-carousel-posts .owl-controls .owl-nav .owl-next,
  .owl-carousel-posts .owl-controls .owl-nav .owl-prev,
  .owl-carousel-fullwidth .owl-controls .owl-nav .owl-next,
  .owl-carousel-fullwidth .owl-controls .owl-nav .owl-prev{
    i {
      color: $brand-black;
    }
    &:hover {
      i {
        color: $brand-black;
      }
    }
  }

  .owl-carousel-fullwidth.fh5co-light-arrow .owl-controls .owl-nav .owl-next,
  .owl-carousel-fullwidth.fh5co-light-arrow .owl-controls .owl-nav .owl-prev {
    i {
      color: $brand-black;
    }
    &:hover {
      i {
        color: $brand-black;
      }
    }
  }


  .owl-theme .owl-controls .owl-nav {
    @media screen and (max-width: $screen-sm) {
      display: none;
    }
  }

  .owl-theme .owl-controls .owl-nav [class*="owl-"] {
    background: none!important;
    i {
      color: lighten($brand-black, 80%);
      font-size: 24px;
      // background: rgba(0,0,0,.5)!important;
      padding: 12px;
      @include transition(.5s);
    }
    &:hover, &:focus {
      i {
        color: $brand-black;
        // background: rgba(0,0,0,.8)!important;
      }
    }
  }
  .owl-theme .owl-dots {
    position: absolute;
    bottom: 0;
    width: 100%;
    text-align: center;
  }
  .owl-carousel-fullwidth.owl-theme .owl-dots {
    bottom: 0;
    margin-bottom: -2.5em;
  }
  .owl-theme .owl-dots .owl-dot span {
    width:10px;
    height:10px;
    background: lighten($brand-black, 80%);
    @include transition(.2s);
    // border: 2px solid transparent;
    &:hover {
      background: none;
      // border: 2px solid lighten($brand-black, 80%);
    }
  }
  .owl-theme .owl-dots .owl-dot.active span, .owl-theme .owl-dots .owl-dot:hover span {
    background: none;
    background: lighten($brand-black, 30%);
    // border: 2px solid $brand-black;
  }

</style>



